import { Component, h, Prop } from '@stencil/core';

@Component({
	tag: 'o-label',
	styleUrls: ['o-label.scss'],
	shadow: false
})
export class OLabel {
	/** The ID of the input element the label is associated with */
	@Prop({ reflect: true }) htmlFor?: string;

	/** The label text to display */
	@Prop() label: string;

	/** Variant for different label states */
	@Prop() variant?: 'error' | 'disabled';

	/** Indicates if the field is required - shows asterisk */
	@Prop() required?: boolean = false;

	render() {
		const baseClasses = 'label-default';
		const disabledClasses = this.variant === 'disabled' ? `${baseClasses} text-content-disabled` : '';
		const errorClasses = this.variant === 'error' ? `${baseClasses} text-content-status-error` : '';
		const defaultClasses = !this.variant ? `${baseClasses} text-content-default` : '';

		return (
			<div>
				<label htmlFor={this.htmlFor} class={`${defaultClasses} ${errorClasses} ${disabledClasses}`}>
					{this.label}
					{this.required ? <span>&nbsp;*</span> : null}
				</label>
			</div>
		);
	}
}
